<template>
  <h1>welcome</h1>
  <label for="name">HEADER</label>
  <input type="text" id="name" @input="(event) => (a = event.target.value)" />
  <label for="name">TYPE</label>
  <input type="text" id="name" @input="(event) => (t = event.target.value)" />
  <br />
  <button @click="fun()">click</button>
  <button @click="del()">del</button>

  <table style="background-color: blue">
    <div v-for="items in ar" v-bind:key="items">
      <label for="name">{{ items }}</label>
      <input
        v-bind:type="tr[items]"
        id="name"
        @input="(event) => (d[items] = event.target.value)"
      />
    </div>
    <button v-on:click="addd()">add</button>
    <button @click="br.pop()">remove</button>
  </table>

  <table style="float: right; background-color: pink">
    <label for="name">Index</label>
    <input
      type="number"
      id="name"
      @input="(event) => (n = event.target.value)"
    />
    <br />
    <label for="name">COLUMN</label>
    <select v-model="c" id="name">
      <option v-for="item in ar" :key="item" v-bind:vlaue="item">
        {{ item }}
      </option>
    </select>
    <br />
    <label for="name">New Value</label>
    <input
      v-bind:type="tr[c]"
      id="name"
      @input="(event) => (nv = event.target.value)"
    />
    <br />
    <button @click="editt()">edit</button>
  </table>

  <br />

  <table class="center">
    <th>SNO.</th>
    <th v-for="x in ar" v-bind:key="x">{{ x }}</th>
    <tr v-for="items in br" v-bind:key="items">
      <td>{{ items.siz }}</td>
      <td v-for="t in ar" v-bind:key="t">{{ items[t] }}</td>
    </tr>
  </table>

  <button @click="downloadCSVData()">download</button>
</template>

<script>
export default {
  name: "card-1",
  data() {
    return {
      d: {},
      a: {},
      ar: [],
      br: [],
      t: {},
      tr: {},
      n: null,
      nv: null,
      c: null,
    };
  },
  methods: {
    fun() {
      const t = Object.assign(this.t);

      this.ar.push(this.a);
      this.tr[this.a] = t;
    },
    del() {
      this.ar.pop();
    },
    addd() {
      const d = Object.assign({}, this.d);
      d["siz"] = this.br.length;
      this.br.push(d);
    },
    downloadCSVData() {
      let tags = this.br;
      let br = tags.map((tag) => Object.values(tag));
      br.splice(0, 0, this.ar);
      var array = br;

      var str = "";
      for (var i = 0; i < array.length; i++) {
        let line = "";
        line = array[i].join(",");
        str += line + "\r\n";
      }
      var blob = new Blob([str], { type: "text/csv;charset=utf-8;" });

      var link = document.createElement("a");
      var url = URL.createObjectURL(blob);
      link.setAttribute("href", url);
      link.setAttribute("download", "csvfilename.csv");
      link.style.visibility = "hidden";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    editt() {
      if (this.n < this.br.length) {
        this.br[this.n][this.c] = this.nv;
      } else {
        alert("Index out of range");
      }
    },
  },
};
</script>

<style scoped>
.center {
  margin-left: auto;
  margin-right: auto;
}
div {
  margin-bottom: 10px;
}
label {
  display: inline-block;
  width: 150px;
  text-align: right;
}
</style>